.logo {
  position: relative;
  display: flex;
  align-items: center;

  font-family: @canvas;
  // font-size: 3rem;
  font-size: 41px;
  letter-spacing: 0.04em;

  .text-stroke(#fff, 0px);

  cursor: pointer;
}

.logo__img-wrap {
  width: 127px;
  height: 127px;
  // width: 92px;
  // height: 92px;
}

.logo__img {
  .img-cover;
}

.logo__title {

  position: absolute;
  bottom: 28px;
  left: 65px;
  width: 110px;

  &:first-letter {
    color: @red;
    text-shadow: none;
    .text-stroke(@brown, 1px);
  }
  &-decor {
    position: relative;
    // width: 6px;
    // height: 6px;
    &::before {
      content: '';
      z-index: 1;
      position: absolute;
      top: 50%;
      left: -9px;

      width: 10px;
      height: 10px;

      background-image: url('../img/svg/logo-star.svg');
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
  }
}

@media (max-width: @tablet-width) {
  .logo {
    font-size: 2rem;
  }
}
